.ColorBox{
  height: 25%;
  width: 20%;
  cursor: pointer;
  display: inline-block;
  position: relative;
  margin: 0 auto;
  margin-bottom: -3.5px;
}

.SingleColourPalette .ColorBox{
  height: 50%;
}

.go-back.ColorBox{
  background-color: #E8E8E8;
}

.back-button,
.copy-button{
  width:100px;
  height: 30px;
  position: absolute;
  display: inline-block;
  margin-left:-50px ;
  margin-top: -15px;
  top: 50%;
  left: 50%;
  text-transform: uppercase;
  background: rgba(255,255,255,0.25);
  color: aliceblue;
  font-size: 1rem;
  line-height: 30px;
  outline: none;
  border: none;
  text-align: center;
  text-decoration: none;
}

.copy-button{
  opacity: 0;
}

.ColorBox:hover .copy-button{
  opacity: 1;
  transition: 0.4s;
}

.box-conatiner {
  position: absolute;
  width: 100%;
  left: 0px;
  bottom: 0px;
  padding: 10px;
  color: black;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 12px;
}
  
.see-more {
  background: rgba(255, 255, 255, 0.3);
  position: absolute;
  border: none;
  right: 0px;
  bottom: 0px;
  color: white;
  width: 60px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
}

.copy-overlay {
  opacity: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  transition: transform 0.6s ease-in-out;
  transform: scale(0.1);
}

.copy-overlay.show {
  opacity: 1;
  transform: scale(50);
  z-index: 10;
  position: absolute;
}

.copy-msg {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 4rem;
    transform: scale(0.1);
    opacity: 0;
    color: white;
  }

  .copy-msg.show {
    opacity: 1;
    transform: scale(1);
    z-index: 25;
    transition: all 0.4s ease-in-out;
    transition-delay: 0.3s;
  }
  
.copy-msg h1 {
  font-weight: 400;
  text-shadow: 1px 2px black;
  background: rgba(255, 255, 255, 0.2);
  width: 100%;
  text-align: center;
  margin-bottom: 0;
  padding: 1rem;
  text-transform: uppercase;
}
.copy-msg p {
  font-size: 2rem;
  font-weight: 100;
}
  